<!-- 내가 기존에 jsp나 html 파일을 만들어서 삽입하던 것을 각각의 vue를 만들어서 삽입하자는 것 -->
<template>
    <div class="submit-form">
        <div v-if="!submitted">
            <div class="form-group">
                <label for="title">Title</label>
                <input
                    type="text"
                    class="form-control"
                    id="title"
                    required
                    v-model="tutorial.title"
                    name="title"
                />
            </div>
            <div class="form-group">
                <label for="description">Description</label>
                <input
                    type="text"
                    class="form-control"
                    id="description"
                    required
                    v-model="tutorial.description"
                    name="description"
                />
            </div>
            <button class="btn btn-success" @click.prevent="saveTutorial">Submit</button>
        </div>
        <div v-else>
            <h4>You successed</h4>
            <button class="btn btn-success" @click.prevent="newTutorial">Add</button>
        </div> 
    </div>
    
</template>

<script>
import TutorialDataService from '../services/TutorialDataService.js';

export default {
    name : "add_tutorial",
    data(){
        return{
            submitted : false,
            tutorial :{
                title : null,
                title : "",
                description : "",
                published : false
            }
        }
    },
    methods : {
        saveTutorial(){
            var datas = {
                title : this.tutorial.title, //보면 v-model로 연결되어 있어 알아서 저기 this.에 동기화 되고 있어
                description : this.tutorial.description
            }
            TutorialDataService.create(datas)
                .then(response=>{
                    this.tutorial.id = response.data.id;
                    console.log(response.data);
                    this.submitted = true;
                })
                .catch(e=>{
                    console.log(e)
                })
        },
        newTutorial(){
            this.submitted = false;
            this.tutorial ={};
        }
    }
}
</script>